@charset 'UTF-8';

@import './setting/variables';
@import './support/right-floating-bar';
@import './common/error';
@import './common/select-your-registered-products';
@import './common/etc';
//20200421 START 장영희 || COVID-19-modal
@import './modal/covid-19-popup';
//20200421 END 장영희 || COVID-19-modal
// @import './modal/header-notice-popup'; // LGEITF-342

// 20200429 START 구유정 || rtl 삭제 (AE 대응)
// 20200429 END 구유정 || rtl 삭제 (AE 대응)

.support-wrap {
	position:relative;
	margin:0;
	width:auto;
	clear:both;
	@extend %clearfix;
	font-size:14px;

	// LGEIN-194 / LGEBR-147
	@include screen(custom, min, 1440) {
		margin: 0 -15px;
		padding-left: 15px;
		padding-right: 15px;
	}
	//** LGEIN-194 / LGEBR-147

	// 20200311 START 구유정 || legend visible hidden (SUPPORT공통)
	legend {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		overflow: hidden;
		clip: rect(0,0,0,0);
		white-space: nowrap;
		border: 0;
	}
	// 20200311 END 구유정 || legend visible hidden (SUPPORT공통)

	&.row {
		width: auto;
	}

	.icon {
		line-height: 0;
	}

	// common option
	.bg-whitesmoke {background: $bg-whitesmoke;}
	.bg-gray {background:#f8f8f8;}
	.bg-white {background:#ffffff;}
	.bg-gray,
	.bg-white {
		&.line {border:1px solid $line-gray;}
	}

	.slick-dots {
		a, button {
			&:focus {
				outline:1px dotted;outline:5px auto -webkit-focus-ring-color;
			}
		}
	}
	
	// Step Box 
	.step-box-default {
		max-width:1284px;
		width:100%;
		background-color:$bg-whitesmoke;
		border:1px solid $line-gray;
		box-sizing:border-box;
		padding:29px 0;  
		text-align:center;
		.step-box-inner {				
			margin:0 auto;
			padding: 0 10px;
			font-size:0;
			@include screen(lg) {
				max-width: 100%;
			} 
			@include screen(md) {
				width:100%;
				max-width: 100%;
				height:100%;
				display:table;
			} 
		}		
		.step {				
			display:inline-block;
			padding-right:60px;
			margin-right:40px;
			font-size:0;
			@include screen(lg) {
				padding-right:40px;
				margin-right:30px;
			}
			@include screen(md) {
				padding-right:30px;
				margin-right:20px;
				vertical-align:middle;
				display:table-cell;
				width:25%; 
			}
			position:relative;
			&.complete:after{
				content:'';
				display:inline-block;
				background:none;
				width:1px;
				height:40px;
				background-color: #e4e4e4;
				position:absolute;
				right:0px;
				top:50%;
				margin-top:-20px;
				@include screen(lg) {
					width:1px;
					height:30px;
					margin-top:-15px;
					right:0px;
				}
				@include screen(md) {
					width:1px;
					height:26px;
					margin-top:-13px;
					right:0px;
				}
			}
			&:after {
				content:'';
				display:inline-block;
				background:url('/lg5-common-gp/images/support/icons/bg-step.svg') no-repeat 0 0;
				width:40px;
				height:40px;
				position:absolute;
				right:-11px;
				top:50%;
				margin-top:-20px;
				background-size:40px 40px;   
				@include screen(lg) {
					background-size:30px 30px;   
					width:30px;
					height:30px;
					margin-top:-15px;
					right:-11px;
				}
				@include screen(md) {
					background-size:26px 26px;   
					width:26px;
					height:26px;
					margin-top:-13px;
					right:10px;
				}
			}				
			&:first-child {
				padding-left:0;
			}
			&:last-child {
				padding-right:0;
				margin-right: 0;
				&:after {
					background:none;
				}
			}
			// 20200429 START 구유정 || rtl 추가
			[dir=rtl] & {
				margin-left:40px;
				margin-right:0;
				padding-left:60px;
				padding-right:0;
				@include screen(lg) {
					padding-left:40px;
					margin-left:30px;
				}
				@include screen(md) {
					padding-left:30px;
					margin-left:20px;
				}
				&.complete:after{
					left:0;
					right:auto;
					@include screen(lg) {left:0px;}
					@include screen(md) {left:0px;}
				}
				&:after {
					left:-11px;
					right:auto;
					transform: rotate(180deg);
					@include screen(lg) {left:-11px;}
					@include screen(md) {left:10px;}
				}
				&:first-child {
					padding-right:0;
				}
				&:last-child {
					margin-left:0;
					padding-left:0;
				}
			}
			// 20200429 END 구유정 || rtl 추가
			.icon {
				position: relative;
				display:inline-block;
				vertical-align: top;
				width:44px;
				height:44px;
				background-color: $bg-nobel;
				border-radius:50%;
				margin-right:18px;
				margin-top: 1px;
				@include screen(md) {
					margin-right:10px;
				}
				// 20200429 START 구유정 || rtl 추가
				[dir=rtl] & {
					margin-left:18px;
					margin-right:0;
					@include screen(md) {
						margin-left:10px;
					}
				}
				// 20200429 END 구유정 || rtl 추가
			}
			&-text {
				display:inline-block;
				vertical-align: middle;
				font-size:16px;
				line-height:44px;
				@include font-family($font-semibold);
				text-align:left;
				@include screen(lg) {
					font-size:14px;
				}
				@include screen(md) {
					font-size:14px;
					width:55%;
				}
				strong {
					display:block;
					font-size:14px;
					color:$color-gray;
					@include font-family($font-bold);
					font-weight:400;
				}
				// 20200429 START 구유정 || rtl 추가
				[dir=rtl] & {
					text-align:right;
				}
				// 20200429 END 구유정 || rtl 추가
			}
			&.active {
				strong {
					color:$color-carmine;
				}
				.icon {
					background-color: $bg-carmine;
				}
			}
		}
	}
	@import './support/feedback-area';
	@import './support/contact-us';

	// Error msg 5.0 스타일
	.error-msg {
		margin-left:0;
		>span{
			position:relative;
			padding:7px 0 0 22px;
			&:before {
				@include pseudo();
				left:0;top:10px;
				width:14px;height:14px;
				background:url('/lg5-common-gp/images/common/icons/notice-carmine.svg') no-repeat 0 0;
			}
		}
		// 20200429 START 구유정 || rtl 추가
		[dir=rtl] & {
			margin-right:0;
			>span {
				padding-left:0;
				padding-right:22px;
				&:before {
					left:auto;
					right:0;
				}
			}
		}
		// 20200429 END 구유정 || rtl 추가
	}

	.notice,
	.error-msg {
		clear:both;
	}
	
	.box-name {
		display:block;
		color: $color-nightrider;
		font-size:24px;
		line-height:32px;
		@include font-family($font-semibold);
		@include screen(custom, max, $max-sm) {&.only-pc {display:none;}}
		@include screen(custom, min, $min-sm) {&.only-mobile {display:none;}}
		& + .box-desc {margin-top:6px;}
		&:not(.only-mobile) + .list {margin-top:21px;}
	}
	@include screen(custom, max, $max-sm) {
		.box-name {
			font-size: 16px;
			line-height: 24px;
			& + .box-desc {margin-top:2px;}
			&:not(.only-pc) + .list .write-type {margin-top:18px;}
			&:not(.only-mobile) + .list {margin-top:0;}
		}
		.list ~ .box-name {
			margin-top:20px;
			padding-top:14px;
			border-top:1px solid $line-lightgray;
		}
	}
	.box-desc {
		&.bg-white {
			padding:14px 20px 13px 20px;
		}
		& + * {margin-top:17px;}
		& + .bg-white {margin-top:23px;}
		@include screen(custom, max, $max-sm) {
			& + * {margin-top:19px;}
		}
	}

	// 공통 스타일 분리
	.write-list-wrap {
		// border:1px solid red;
		.list {
			@extend %clearfix;
			& ~ .list {
				margin-top:24px;
			}
			&.textarea-lay  {
				margin-top: 9px;
				@include screen(custom, min, $min-sm) {
					margin-bottom: 4px;
				}
			}
			&.update-check {
				margin-top:26px;
				border-bottom:1px solid $line-lightgray;
				padding-bottom:30px;
				@include screen(custom, max, $max-sm) {
					padding-bottom:15px;
				}
				& + .list {margin-bottom:22px;}
			}
			@include screen(custom, max, $max-sm) { 
				&:nth-child(n) {margin-top: 0;}
				&:first-child .write-type:first-child {
					margin-top:0;
				}
			}
			.box-title {
				display: block;
				color: $color-nightrider;
				font-size: 16px;
				line-height: 24px;
				// margin-bottom:0;
				@include font-family($font-semibold);
				@include screen(custom, max, $max-sm) {
					font-size: 14px;
					line-height: 20px;
				}
			}
			.write-type {
				position:relative;
				width: calc(50% - 20px);
				float: left;
				&.full {
					width: 100%;
					float: none;
				}
				&:nth-child(odd) {
					margin-right:40px;
				}
				@include screen(custom, max, $max-sm) { 
					width: 100%;
					margin-top: 14px;
					&:nth-child(n) {
						float: none;
					}
					&.int02 {
						margin-top: 20px;
						margin-bottom: -5px;
					}
					&:nth-child(odd) {
						margin-right:0;
					}
				}
				input[disabled]::-ms-input-placeholder { color: $color-nightrider; }
				input[disabled]::-webkit-input-placeholder { color: $color-nightrider; } 
				input[disabled]::-moz-placeholder { color: $color-nightrider; }
				.datepicker-wrap {
					overflow:hidden;
				}
			}
			.input-info-text {
				font-size:14px;
				line-height:20px;
				color:$color-dimgray;
				margin:4px 0 1px 10px;
				@include screen(custom, max, $max-sm) {
					br {display:none;}
					padding-bottom: 5px;
					margin-bottom: 0;
				}
			}
		}
		.policy-check {
			margin-top:30px;
			padding-top:24px;
			border-top:1px solid $line-lightgray;
			.list ~ .list {margin-top:9px;}
			@include screen(custom, max, $max-sm) {
				margin-top:15px;
				padding-top:14px;
				.list ~ .list {margin-top:0;}
				.list .write-type {margin-top:14px;}
			}
			.policy-name {
				display:block;
				color:$color-black;
				font-size:20px;
				& + .list {margin-top:14px;}
			}
			@include screen(custom, max, $max-sm) {
				.policy-name {
					font-size:16px;
					line-height:24px;
					& + .list {margin-top:0;}
					& + .list .write-type {margin-top:7px;}
				}
			}
			.check-area {
				padding-left:34px;
				.checkbox-box {margin-left:-34px;}
				.link-text {margin-left:5px}
			}
		}
		/* LGEPL-317 : start  */
		.policy-gdpr {
			font-size: 16px;
			.policy-text {
				color: $color-dimgray;
				font-weight: 400;
				@include screen(custom, max, $max-sm) {
					font-size: 14px;
				}
			}
			.write-type {
				margin-top: 10px;
				@include screen(custom, max, $max-sm) {
					margin-top: 5px;
				}
			}
		}
		/* LGEPL-317 : end  */
	}

	.repair-info-wrap .table-view {
		strong { 
			color:#000;
		}
	}

	// 20200423 START 구유정 || rtl 추가
	[dir="rtl"] & { // .support-wrap
		.error-msg > span {
			padding-left:0;
			padding-right:22px;
			&:before {
				left:auto;
				right:0;
			}
		}
		.write-list-wrap {
			.list {
				.write-type {
					float:right;
					&.full {float:none;}
					&:nth-child(odd) {
						margin-left:40px;
						margin-right:0;
					}
					@include screen(custom, max, $max-sm) {
						&:nth-child(n) {float:none;}
						&:nth-child(odd) {margin-left:0;}
					}
				}
			}
			.policy-check {
				.check-area {
					padding-left:0;
					padding-right:34px;
					.checkbox-box {
						margin-left:0;
						margin-right:-34px;
					}
					.link-text {
						margin-left:0;
						margin-right:5px;
					}
				}
			}
		}
	}
	// 20200423 END 구유정 || rtl 추가
}

[dir="rtl"]{	
	// RTL slick dot first button
	.support-wrap {
		.solutions-contents {
			.product-type-gp {
				.slick-slider {
					.slick-dots li:first-child {
						margin-left:10px;
					}
				}
			}
		}
	}
}
