.covid-19,.notification-popup{
	.repair-area {
		font-size: 16px;
		line-height: 1.5;
		@include screen(custom, max, $max-sm) {
			font-size: 14px;
			line-height: 21px;
		}
	}
	.bg-gray{
		padding: 27px 30px;
		margin-top:9px;
		@include screen(custom, max, $max-sm) {
			padding:20px;
		}
	}
	.info-desc{
		& + .info-desc{
			margin-top:9px;
		}
	}
	.modal-footer{
		border-top:1px solid #e4e4e4;
		margin:23px 10px 0;
		padding: 20px 20px 30px;
	}
	//20200511 START 장영희 || ca_en, ca_fr check box add
	.modal-check{
		margin: 0 0 20px;
		.checkbox-box .text {
			text-align: left;
			@include screen(custom, max, $max-sm) {
				font-size: 14px;
    			line-height: 20px;
			}
		}
	}
	//20200511 END 장영희 || ca_en, ca_fr check box add
	.dot-list {
		>li{
			line-height: 20px;
			position: relative;
			padding-left: 14px;
			&:before{
				display: inline-block;
				content: "";
				width:4px;
				height: 4px;
				background: #a50034;
				border-radius: 2px;
				position: absolute;
				top: 9px;
				left: 0;
				@include screen(custom, max, $max-sm) {
					top: 9px;
				}
			}
			& + li{
				margin-top:10px;
			}
		}
	}
	.modal-info-area{
		& + .info-desc{
			margin-top:9px;
		}
	}
	.mCSB_container {
		padding-left:10px;padding-right:16px;
		@include screen(custom, max, $max-sm) {padding-left:16px;padding-right:7px;}
	}
	.mCSB_scrollTools {
		right:5px;
		@include screen(custom, max, $max-sm) {right:3px;}
	}
	//20200511 START 장영희 || ca_en, ca_fr check box add
	.js-scroll {
		max-height:425px;
		@include screen(custom, max, $max-sm) {
			max-height:200px;
		}
	}
	//20200511 END 장영희 || ca_en, ca_fr check box add
	
	.modal-body {max-height:inherit; padding: 0 20px; color: #333;}
	//20200421 START 장영희 || repair, warranty COVID PopUp
	.bold-pop{
		display: inline-block;
		font-weight:600;
		margin-top: 9px;
	}
	.bg-gray{
		background: #f8f8f8;
	}
	//20200525 START 장영희 || PH font 수정
	.font-bold-italic{
		font-weight: 600;
		font-size: 17px;
		font-style: italic;
		@include screen(custom, max, $max-sm) {
			font-size: 15px;
		}
	}
	//20200525 START 장영희 || PH font 수정

	// 20200511 START 장영희 || COVID popup RTL
	[dir="rtl"] & { // .covid-19
		.modal-check{
			.checkbox-box .text {
				text-align: right;
				padding-left: 0;
				padding-right: 26px;
			}
		}
		.dot-list {
			>li{
				padding-left: 0;
				padding-right: 14px;
				&:before{
					left: auto;
					right:0;
				}
			}
		}
		.mCSB_container {
			padding-left:16px;
			padding-right:10px;
			margin-right: 0;
			margin-left: 18px;
			@include screen(custom, max, $max-sm) {
				padding-left:7px;padding-right:16px;
			}
		}
		.mCSB_scrollTools {
			right:auto;
			left: 5px;
			@include screen(custom, max, $max-sm) {
				right:auto;
				left: 3px;
			}
		}
	}
	// 20200511 END 장영희 || COVID popup RTL
}
.covid-links{
	margin: 30px 0;
	.covid-link{
		text-align:center;
	}
	//20200421 END 장영희 || repair, warranty COVID PopUp
}
#covid-19-pop2{
	.modal-header{
		border: 0;
		margin: 0 30px 5px;
	}
}