// Right floating bar

// html scroll-y banding on now position
.band-scroll {
	@include screen(custom, max, 1440) {
		position : relative;
		overflow: hidden;
	}
}

.right-floating-bar {
	// area variable
	$floatingbar-bgcolor:$color-white; // $ffffff
	$floatingbar-cube-bgcolor:$color-black; //#000000
	$floatingbar-bordercolor:$color-nightrider; // #333333
	$product-font-color :$color-dimgray; // #4e4e4e
	$pagi-arrow-color:$line-gray; // #cecece
	$floatingbar-rounding : 3px;

	@mixin cubic($size){
		width:$size;
		height:$size;
	}
	// inheritance
	z-index:100;
	@include font-family($font-semibold);
	color:$color-white;

	// default
	ul, ol, li {
		@extend %listreset;
	}
	a {
		display:block; 
		width:100%; height:100%;
		color:inherit;
		text-decoration:none;
	}
	em {font-style:normal;}
	button {
		padding: 0;
		background-color:transparent;
		border:none;
	}
	img {
		width:100%;
		vertical-align:top;
	}
	svg {
		fill : currentColor;
		vertical-align: top;
	}
	.head-title, .icon {
		display:inline-block;
		vertical-align: middle;
	}

	/* LGEAU-67 20200601 Changing How Support Sidebar Behaves */
	.unfold-wrap {
		position: absolute;
		right: 0;
		top: 0;
		margin-top: -26px;
		text-align: right;
		line-height: 1;
	}
	.btn-unfold-close {
		display: inline-block;
		padding: 3px;
		background-color: $floatingbar-cube-bgcolor;
		color: $floatingbar-bgcolor;
		line-height: 1;
		cursor: pointer;
		&:before {
			content: "";
			display: block;
			width: 22px;
			height: 22px;
			background: url(/lg5-common-gp/images/common/icons/link-fold.svg) no-repeat 50% 50%;
			background-color: transparent;
			background-size: 100%;
		}
	}
	@include screen(custom, max, 1439) {
		.unfold-wrap {
			display: none;
		}
	}
	/* // LGEAU-67 20200601 Changing How Support Sidebar Behaves */

	// slider default
	.slick-slider {
		position: relative;
		display: block;
		box-sizing: border-box;
		-webkit-touch-callout: none;
		-webkit-user-select: none;
		-khtml-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
		-ms-touch-action: pan-y;
		touch-action: pan-y;
		-webkit-tap-highlight-color: transparent;
		.slick-track, .slick-list {
			-webkit-transform: translate3d(0, 0, 0);
			-moz-transform: translate3d(0, 0, 0);
			-ms-transform: translate3d(0, 0, 0);
			-o-transform: translate3d(0, 0, 0);
			transform: translate3d(0, 0, 0);
		}
		.slick-list {
			position: relative;
			overflow: hidden;
			display: block;
			margin: 0;
			padding: 0;
		
			&:focus {
				outline: none;
			}
		}
		.slick-track {
			position: relative;
			left: 0; top: 0;
			display: block;
			margin:0 auto;
		
			&:before,&:after {
				content: "";
				display: table;
			}
		
			&:after {
				clear: both;
			}
		}
		.slick-slide {
			float: left;
			height: 100%;
			min-height: 1px;
			[dir="rtl"] & {
				float: right;
			}
			&.dragging img {
				pointer-events: none;
			}
		}
		&.slick-initialized {
			display: block;
		}
	}
	.rolling-area {
		display:none;
		max-height:230px;
		overflow:hidden;
	}

	//desktop
	@include screen(custom, min, 1440) {
		position: absolute;
		top:276px; // LGEIN-194 / LGEBR-147
		right:0;
		//overflow:hidden;
		text-align:right;
		border-radius:$floatingbar-rounding;
		//inheritance
		font-size:14px;
		line-height: 20px;

		&.fixed {
			position: fixed;
			top: 110px;
		}
		.call-bar-menu-list {
			display:none;
		}
		.bar-menu-list {
			width:182px;
			text-align:left;
			border-radius:$floatingbar-rounding;

			// animation
			transition:margin 300ms ease-out;
		}
		.bar-menu {
			width:100%;
			background-color:$floatingbar-cube-bgcolor
		}
		.bar-menu:first-child{
			border-top-left-radius:$floatingbar-rounding;
			border-top-right-radius:$floatingbar-rounding;
		}
		.bar-menu:last-child{
			border-bottom-left-radius:$floatingbar-rounding;
			border-bottom-right-radius:$floatingbar-rounding;
		}
		.bar-menu.recently-viewed{
			background-color:$floatingbar-bgcolor;
			color:$floatingbar-cube-bgcolor;
		}
		.bar-menu-head {
			position:relative;
			width:100%;
			height:50px;
			border-bottom:1px solid $floatingbar-bordercolor;
			box-sizing:content-box;
			.head-title {
				margin-left: 8px;
				max-width: 109px;
				// word-break: break-all;
				word-break: break-word;
				overflow: hidden;
				max-height: 40px;
				text-overflow: ellipsis;
				display: -webkit-inline-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
			}
			.icon {
				height: 50px;
				display: inline-block;
				position: relative;
				padding: 15px 0 15px 15px;
			}
			.icon img, .icon svg {
				@include cubic(24px);
				padding: 2px;
			}
		}
		// exception style
		.bar-menu-list.fold .bar-menu.livechat .icon {padding-left:18px;}
		.bar-menu.livechat .icon {padding-left:13px;}
		.bar-menu.livechat .icon svg {
			width:24px; height:21px;
		}
		// recently-viewed
		
		.bar-menu.recently-viewed {
			border:1px solid $floatingbar-cube-bgcolor;
			background-color:$floatingbar-bgcolor;
			color:$floatingbar-cube-bgcolor;
			.not-yet-view, .rolling-area {
				border-top:1px solid $floatingbar-cube-bgcolor;
				border-bottom:1px solid $floatingbar-cube-bgcolor;
			}
			.not-yet-view {
				display:none;
				text-align:center;
				&.active {
					display:block;
				}
				.guide-paragraph {
					padding:52px 0;
				}
				.guide-text {
					display:inline-block;
					width:100%;
					font-size:12px;
					line-height: 14px;
					margin-top:16px;
				}
				.icon {
					display:block;
					width:64px;
					margin:0 auto 18px;
				}
			}
			.head-inner {cursor:default;}
			.head-inner:hover .head-title {
				text-decoration: none;
			}
			.bar-menu-head {
				border-bottom:none;
			}
			.bar-menu-head .count-viewed {
				position: absolute;
				top:7px; right:-8px;
				@include cubic(18px);
				background-color:$color-carmine;
				@include font-family($font-light);
				font-size:12px;
				color:$color-white;
				line-height:18px;
				text-align:center;
				border-radius:50%;

			}
			.bar-menu-detail {
				padding:0 15px;
			}
			.viewed-bundle {
				.viewed-item {
					margin-top:12px;
					font-size:0;
				}
				.viewed-item:last-child {margin-bottom:12px;}
				.imagbox, .model-number {
					display:inline-block;
					vertical-align:middle;
				}
				.imagbox {
					width:60px;
				}
				.model-number {
					width:calc(100% - (60px + 10px));
					white-space:nowrap;
					overflow:hidden;
					text-overflow:ellipsis;
					margin-left:10px;
					font-size:12px;
					line-height: 20px;
					color:$product-font-color;
				}
			}
			// recently viewed controller
			.viewed-bundle-handler {
				padding:5px 0 22px;
			}
			.status-bundle-location {
				display:inline-block;
				vertical-align:top;
				.stand {color:$color-carmine;}
			}
			.quiver {
				float:right;
				margin-top:5px;
				// use slick :: standard
				.slick-arrow {
					position: relative;
					@include cubic(20px);
					background-color:$floatingbar-bgcolor;
					border:1px solid $pagi-arrow-color;
					text-align: center;
					// default text hiding
					white-space: nowrap;
					overflow: hidden;
					color:transparent;
					text-indent:-9999em;
					line-height: 0;
					// icon
					background-position:50% 50%;
					background-size:20px 20px;
					background-repeat:no-repeat;
				}
				.slick-prev {
					background-image:url(/lg5-common-gp/images/common/icons/arrow-left.svg);
					margin-right:4px;
				}
				.slick-next {
					background-image:url(/lg5-common-gp/images/common/icons/arrow-right.svg);
				}

				// to generate button elements :: option custom
				font-size:16px; // icon size 
				[class*="-bundle"]{
					display:inline-block;
					vertical-align: top;
					@include cubic(20px);
					background-color:$floatingbar-bgcolor;
					border:1px solid $pagi-arrow-color;
					text-align: center;
					cursor: pointer;
				}
				.guidetext {
					position: absolute;
					z-index:-100;
					left:-100000px;
					opacity: 0;
				}
			}
		}
		// end expand
		
		// area status :: fold
		.bar-menu-list.fold{
			margin-right : -122px;
			.bar-menu-head .head-title {
				margin-left:25px;
			}
			.bar-menu-head .icon {
				padding: 15px 0 15px 18px;
			}
		}
		.to-egg {
			&.bar-menu.recently-viewed .bar-menu-head {
				background-color:$floatingbar-cube-bgcolor;
				color:$floatingbar-bgcolor;
			}
			.bar-menu-detail {
				display:none;
			}
		}
		// end fold

		// back to top button
		&.fixed {
			.back-to-top {
				opacity:1;
				transform: translateX(0);
				z-index:100;
			}
		}
		.back-to-top {
			position:fixed;
			transition:opacity 0.8s ease-out, transform 0.5s ease-out;
			right:20px;
			bottom:20px;
			opacity:0;
			z-index:-100;
			transform: translateX(110px);
			width:50px;
			height:50px;
			$mkt-float : (
				bg-graywhite : #e5e5e5,
				bg-black : #333333
			);
			background:url(/lg5-common-gp/images/common/icons/arrow-up-nightrider.svg) 50% 50% no-repeat rgba( map-get($mkt-float, bg-graywhite), 0.75) ;
			background-size:20px 20px;
			border-radius:50%;
			margin-top:15px;
			line-height:0;
			button{
				width:100%;
				height:100%;
				padding:0;
				border:none;
				background:none;
				cursor:pointer;
			}
			.icon {display:none;}
			.top-text {width:0;height:0;overflow:hidden;display:block;}
		}
	}

	// mobile
	@include screen(custom, max, 1439) {
		// extend
		%shapemenu {
			@include cubic(40px);
			border-radius:50%;
		}
		position:fixed;
		// test
		bottom:2vh; right:15px;
		max-height:100vh;
		// test
		font-size:14px;
		.bar-menu-list{margin-bottom: 10px;}
		.bar-menu.recently-viewed{
			display:none;
		}
		.bar-menu{
			@extend %shapemenu;
			position:relative;
			background-color:$floatingbar-bgcolor;
			&:not(:first-child){
				margin-top: 15px;
			}
			.bar-menu-head {
				height:100%;
				.head-title{
					position: absolute;
					top: 50%; transform: translateY(-50%);
					right: 100%; margin-right: 10px;
					-webkit-transform: translateY(-50%);
    				-moz-transform: translateY(-50%);
    				-o-transform: translateY(-50%);
					width: 230px;
					overflow: hidden;
					text-align: right;
					text-overflow: ellipsis;
					word-break: break-all;
					display: -webkit-inline-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
				}
				.icon {
					position: absolute;
					top:50%; left:50%;
					transform:translate(-50%, -50%);
					color:$floatingbar-cube-bgcolor;
				}
			}
			// exception style
			&.livechat .icon {margin-top: -1px;}
		}
		.call-bar-menu-list{
			@extend %shapemenu;
			display:block;
			background-color:$color-carmine;
			.icon {max-width:20px;}
		}
		// back to top button
		.back-to-top {
			display:inline-block;
			margin-top: 3px;
			margin-top: 5px;
			width:40px;
			height:40px;
			overflow:hidden;
			border-radius:50%;
			background-color:$color-dimgray;
			color:$floatingbar-bgcolor;
			text-align: center;
			@extend %shapemenu;
			.icon {max-width:20px;}
			.top-text {
				display:block;
				text-transform: uppercase;
				margin:-6px 0 3px;
				@include font-family($font-bold);
				font-size:11px;
			}
		}
		.back-layer-by-mobile {
			display:none;
			position:fixed; z-index:-1;
			top:0; bottom:0; left:0; right:0;
			background-color:rgba(0,0,0,0.7);
			&.active{
				display:block;
			}
		}

		// animation
		.bar-menu-list {
			position:absolute;
			visibility:hidden;
			opacity:0;
			transition:opacity 0s 0s ease-out;
		}
		.call-bar-menu-list, .call-bar-menu-list .icon {
			transition:transform 300ms 100ms ease-out;
		}
		.back-to-top{
			transition:opacity 300ms;
		}
		// active menu list
		&.active {
			.bar-menu-list{
				position:static;
				visibility:visible;
				opacity:1;
				height:auto;
				transition:transform 300ms 100ms ease-out, opacity 500ms 500ms ease-out;
			}

			// animation
			.call-bar-menu-list .icon {
				transform: rotate(-135deg);
			}
			.bar-menu-list, .call-bar-menu-list {
				transform: translateY(45px);
			}
			.back-to-top{
				position:relative; z-index:-1;
				opacity:0;
			}
		}
	}
	// print
	@include screen(print){
		display:none;
	}
}
//  right-floating-bar RTL
[dir="rtl"]{
	.right-floating-bar {
		//desktop
		@include screen(custom, min, 1440) {
			right:auto;
			left:0;
			text-align:left;
			overflow:hidden;
			border-radius: 0.00001px;	// IE TOP button click fixed
			.bar-menu-list {
				text-align:right;
			}

			.bar-menu-head {

				.head-title {
					margin-left: auto;
					margin-right: 8px;
				}
				.icon {
					padding: 15px 15px 15px 0px;
				}
			}

			.bar-menu-list.fold{
				margin-right : auto;
				margin-left : -122px;


				.bar-menu-head .head-title {
					margin-left:auto;
					margin-right:25px;
				}
				.bar-menu-head .icon {
					padding: 15px 18px 15px 0px;
				}
			}

			.bar-menu.recently-viewed {
				.bar-menu-head .count-viewed {
					right:auto;
					left: -8px;
				}
				.quiver {
					float:left;
				}
			}
		}
		@include screen(custom, max, 1439) {
			right:auto;
			left:15px;

			.bar-menu{
				.bar-menu-head {
					.head-title{
						text-align:left;
						right:auto;
						left:100%;
						margin-right:auto;
						margin-left:10px;
					}
				}
			}


		}

		.bar-menu.recently-viewed {
			.quiver {
				.slick-prev {
					background-image:url(/lg5-common-gp/images/common/icons/arrow-right.svg);
					margin-right:auto;
					margin-left:4px;
				}
				.slick-next {
					background-image:url(/lg5-common-gp/images/common/icons/arrow-left.svg);
				}
			}
		}


		.back-to-top {
			transform: translateX(-110px);
			right:auto;
			left:20px;
		}

		&.fixed {
			.back-to-top {
				transform: translateX(0px);
			}
		}

		
	}
}


.feedback-floating-wrap {
	position:fixed;
	bottom:0;
	left:0;
	right:0;
	margin:0 auto;
	width:100%;
	max-width:1600px;
	height:0;
	z-index:99999;
	.feedback-floating-banner {
		position:absolute;
		bottom:0;
		right:0;
		transform-origin:bottom right;
		@include screen(custom, max, $max-sm) {
			position:fixed;
			bottom:0;
			left:0;
			right:0;
			margin:0 auto;
			transform-origin:bottom center;
		}
	}
	// 20200423 START 구유정 || rtl 추가
	[dir="rtl"] & { // .feedback-floating-wrap
		.feedback-floating-banner{
			left:0;
			right:auto;
			@include screen(custom, max, $max-sm) {
				right:0;
			}
		}
	}
	// 20200423 END 구유정 || rtl 추가
}

.feedback-floating-banner {
	display:block;
	width:274px;
	border:1px solid $line-darkgray;
	background:$bg-white;
	transition:transform 0.25s cubic-bezier(0.22, 0.61, 0.36, 1);
	&.waiting {transform:scale(0);}
	&.show {transform:scale(1);}
	&.hidden {
		display:none;
		visibility:hidden;
	}
	&.only-pc.show {
		display:block !important;
		visibility:visible !important;
	}
	&.only-mobile.show {
		display:none !important;
		visibility:hidden !important;
	}
	@include screen(custom, max, $max-sm) {
		&.only-pc.show {
			display:none !important;
			visibility:hidden !important;
		}
		&.only-mobile.show {
			display:block !important;
			visibility:visible !important;
		}
	}

	p {margin:0}
	.banner-image {
		img {max-width:100%;}
	}
	.banner-title {
		padding:8px 23px;
		color:$color-white;
		background-color:$bg-nightrider;
		&:first-child {padding-right:50px;}
		.title {
			font-size:20px;
			line-height:26px;
		}
	}
	.banner-content {
		padding:19px 24px 25px 24px;
		.text-area + .text-area {
			margin-top: 25px;
			padding-top: 19px;
			border-top:1px solid #f8f8f8;
		}
		.text {
			font-size:14px;
			line-height:20px;
			color:$color-nightrider;
		}
		.btn {
			margin-top:10px;
			width:100%;
			/* LGECI-293 20201104 edit */
			&:focus{
				box-shadow:0 0 0 0 !important;
				outline: 3px solid #000;
				outline-offset: -3px;
			}
			/* //LGECI-293 20201104 edit */
		}
	}
	.banner-checkbox {
		padding:11px 24px 13px 24px;
		background-color:$bg-whitesmoke;
		.checkbox-box {
			min-height:18px;
		}
		.text {
			font-size:14px;
			line-height:18px;
		}
	}
	.banner-close {
		position:absolute;
		top:-1px; 
		right:-1px;
		.close {
			display:block;
			padding:17px 16px 15px 16px;
			background-color:$bg-nightrider;
			&:before {
				content:"";
				display:block;
				width:12px;
				height:12px;
				background:url('/lg5-common-gp/images/common/icons/link-fold.svg') no-repeat 50% 50%;
			}
		}
	}
	
	// 20200423 START 구유정 || rtl 추가
	[dir="rtl"] & { // .feedback-floating-banner
		.banner-title {
			&:first-child {
				padding-left:50px;
				padding-right:23px;
			}
		}
		.banner-close {
			left:0;
			right:auto;
		}
	}
	// 20200423 END 구유정 || rtl 추가
}