.contact-us-link-menu {
	position:relative;
	margin:0 -15px;
	padding:50px 0 64px;
	padding-top:40px; // line default 적용
	border-top:1px solid $line-gray !important; // line default 적용
	&.line {
		padding-top:40px;
	}
	@include screen(print) {
		display:none;
	}
	@include screen(custom, min, $min-md) {
		&.line {
			border-left:0 !important;
			border-right:0 !important;
			border-bottom:0 !important;
		}
		&.line.bg-gray {
			border-bottom:1px solid $line-gray !important;
		}
	}
	@include screen(custom, max, $max-lg) {
		margin:0 -15px;
	}
	@include screen(custom, max, $max-sm) {
		background:$bg-white !important;
		border:0 !important;
		padding:30px 0 0;
		padding-top:30px; // line default 적용
		&.line {
			padding-top:30px;
		}
	}
	// top thick line
	@include screen(custom, max, $max-sm) {
		&:before {
			@include pseudo();
			width:100%;
			height: 20px;
			background: #f2f2f2;
			margin: 0 0 20px;
			box-sizing:content-box;
		}
	}
	.inner {
		max-width:(960px+60px);
		padding:0 30px;
		margin:0 auto;
		@include screen(custom, max, $max-sm) {
			padding:40px 24px 0;
		}
	}
	// max-width
	.area-title,
	.area-title ~ .desc {
		max-width:565px;
		margin:0 auto;
		@include screen(custom, max, $max-sm) {
			max-width:100%;
		}
	}
	// title
	.area-title {
		@include font-family($font-semibold);
		font-size:32px;
		line-height:40px;
		text-align:center;
		display:block;
		color:$color-black;
		margin-bottom:5px;
		@include screen(custom, max, $max-sm) {
			font-size:28px;
			line-height:36px;
			margin-bottom:1px;
		}
	}
	// desc
	.area-title ~ .desc {
		@include font-family($font-regular);
		font-size:16px;
		line-height:24px;
		text-align:center;
		color:$color-dimgray;
		@include screen(custom, max, $max-sm) {
			font-size:14px;
			line-height:20px;
		}
	}
	//20200421 START 장영희 || CA_EN, CA_FR Mo Ver Contac Us Class Add > .origin-tit-mo Add
	.area-title.origin-tit-mo ~ .desc{
		@include screen(custom, max, $max-sm) {
			margin-top:7px;
		}
	}
	//20200421 END 장영희 || CA_EN, CA_FR Mo Ver Contac Us Class Add > .origin-tit-mo Add
	// icons 
	.menu-linkbox-belt {
		position:relative;
		margin:28px 0 0;
		display:table;
		width:100%;
		table-layout:fixed;
		@include screen(custom, max, $max-sm) {
			display:block;
			margin:21px 0 26px;
		}
		.menu-linkbox {
			display:table-cell;
			margin:0;
			@extend %clearfix;
			@include screen(custom, max, $max-sm) {
				clear:both;
				display:block;
				width:auto;
				border-top:1px solid $line-lightgray;
				&:first-child {
					border-top:0;
				}
				margin-right:-6px;
				padding-right:6px;
				background:url('/lg5-common-gp/images/common/icons/arrow-right-nightrider.svg') no-repeat right 50%; 
			}
			a {
				display:block;
				width:100%;
				text-decoration:none;
				@extend %clearfix;
				// &:focus .menu-title,
				// &:hover .menu-title {text-decoration:underline;}
				@include screen(custom, max, $max-sm) {
					padding:14px 0;
					// margin:14px 0;
				}
			}
			.icon {
				display:block;
				text-align:center;
				width:50px;
				height:50px;
				margin:0 auto;
				img {
					width:100%;
				}
				@include screen(custom, max, $max-sm) {
					float:left;
					width:20px;
					height:20px;
					line-height:0;
				}
			}
			.menu-title {
				display:block;
				text-align:center;
				padding-top:15px;
				font-size:20px;
				line-height:24px;
				color:$color-nightrider;
				@include font-family($font-semibold);
				@include screen(custom, max, $max-sm) {
					float:left;
					font-size:16px;
					height:20px;
					line-height:20px;
					padding-top:0;
					padding-left:13px;
				}
			}
			
			p.desc,
			p.desc a {
				margin:0;
				font-size:14px;
				line-height:20px;
				text-align:center;
				color:$color-dimgray;
				@include font-family($font-regular);
				@include screen(custom, max, $max-sm) {
					display:none;
				}
			}
			p.desc:first-of-type {
				margin-top:9px;
			}

			
			// 20200427 START 구유정 || .underline 추가
			.underline {
				text-decoration:underline;
			}
			// 20200427 END 구유정 || .underline 추가

		}
		//20200421 START 장영희 || CA_EN, CA_FR Mo Ver Contac Us Class Add > .origin-str-mo Add
		&.origin-str-mo{
			.menu-linkbox p.desc{
				.only-mobile{
					display: none;
				}
				.only-pc{
					display: block;
				}
			}
			@include screen(custom, max, $max-sm) {
				margin: 4px 0 26px;
				display: table;
				.menu-linkbox{
					margin: 0;
					background: none;
					margin-top:25px;
					padding-right: 0;
					&:first-child{
						margin-top:0;
					}
					a{
						padding-top: 30px;
					}
					.menu-title{
						float:none;
						padding-top: 15px;
						padding-left: 0;
						font-weight: 700;
						font-size: 20px;
						line-height: 1.2;
					}
					.icon{
						float: none;
						width: 50px;
						height: 50px;
					}
					.icon-tit-wrap{
						padding: 30px 0 14px;
					}
				}
				.menu-linkbox p.desc{
					.only-mobile{
						text-decoration: underline;
						display: block;
					}
					.only-pc{
						display: none;
					}
					&:first-of-type{
						margin-top:15px;
					}
					>a{
						text-decoration: underline;
					}
				}
				.menu-linkbox p.desc, .menu-linkbox p.desc a{
					display:block;
					padding: 0;
				}
			}
		}
		//20200421 END 장영희 || CA_EN, CA_FR Mo Ver Contac Us Class Add > .origin-str-mo Add
	}
	
	// 20200603 START 구유정 || rtl 추가
	[dir="rtl"] & { // .contact-us-link-menu
		.menu-linkbox-belt:not(.origin-str-mo) {
			.menu-linkbox {
				@include screen(custom, max, $max-sm) {
					background:url(/lg5-common-gp/images/common/icons/arrow-left-nightrider.svg) no-repeat left 50%;
				}
				.icon {
					@include screen(custom, max, $max-sm) {
						float:right;
					}
				}
				.menu-title {
					@include screen(custom, max, $max-sm) {
						float:right;
						padding-left:0;
						padding-right:13px;
					}
				}
			}
		}
	}
	// 20200603 END 구유정 || rtl 추가
}
// with feedback area
.feedback-area + .contact-us-link-menu {
	// padding-top:40px;
	padding-top:50px;
	@include screen(custom, max, $max-sm) {
		padding:0;
		margin-top:-11px;
	}
}

[countrycode=ca_en],
[countrycode=ca_fr] {
	.support-wrap .contact-us-link-menu .menu-linkbox-belt .menu-linkbox{
		@include screen(custom, max, $max-sm) {
			background:none;
		}
		.icon {
			@include screen(custom, max, $max-sm) {
				float: none;
				width: 50px;
				height: 50px;
				line-height: 0;
			}
		}

		.menu-title {
			@include screen(custom, max, $max-sm) {
				float: none;
				font-size: 16px;
				height: auto;
				line-height: 20px;
				padding-top: 0;
				padding-left: 0px;
			}
		}

		p.desc {
			@include screen(custom, max, $max-sm) {
				display:block;
			}
		}
		// 20200603 START 구유정 || rtl 추가
		[dir="rtl"] & { // .menu-linkbox
			@include screen(custom, max, $max-sm) {
				background: none;
			}
			.icon {
				@include screen(custom, max, $max-sm) {
					float: none;
				}
			}
			.menu-title {
				@include screen(custom, max, $max-sm) {
					float: none;
					padding-left:0;
					padding-right:0;
				}
			}
		}
		// 20200603 END 구유정 || rtl 추가
	}
}

