.error-common {
	position: relative;
	overflow: hidden;
	text-align: center;
	.guide-error {
		padding: 154px 0 198px 0;
		@include screen(custom, max, $max-sm) {
			padding: 80px 9px 73px 9px;
		}
	}
	.guide-title {
		margin-bottom: 17px;
		font-size: 40px;
		line-height: 48px;
		color: $color-black;
		@include font-family($font-semibold);
		@include screen(custom, max, $max-sm) {
			margin-bottom: 15px;
			font-size: 24px;
			line-height: 28px;
			br {display: none;}
		} 
	}
	.guide-subtext {
		font-size: 18px;
		line-height: 24px;
		color: $color-nightrider;
		@include screen(custom, max, $max-sm) {
			font-size: 16px;
			line-height: 24px;
			color: $color-dimgray;
		}
	}
	.icon {
		display: block;
		margin-bottom: 22px;
		@include screen(custom, max, $max-sm) {
			margin-bottom: 16px;
		}
		img {
			width: 80px;
			height: 80px;
			@include screen(custom, max, $max-sm) {
				width: 60px;
				height: 60px;
			}
		}
	}
	.extra-primary {
		color: $color-carmine;
		text-decoration: underline;
	}
}

.tabs-contents-wrapper {
	.error-common {
		.icon {
			img {
				width: 70px;
				height: 70px;
			}
		}

		.guide-title {
			font-size: 32px;
			line-height: 40px;
			display: block;
		}
		.guide-subtext {
			color: $color-dimgray;
			font-size: 16px;
			line-height: 24px;
		}
	}
}

.error-wrapper {
	.error-common {
		.guide-error {
			padding: 25px 0;
		}
		.icon {
			img {
				width: 60px;
				height: 60px;
			}
			margin-bottom: 16px;
		}
		.guide-title {
			font-size: 20px;
			line-height: 24px;
			color: #000;
			margin: 0 0 8px;
			display: block;
		}
		.guide-subtext {
			font-size: 14px;
			line-height: 20px;
			color: $color-dimgray;
			margin: 0;
		}
	}
	@include screen(custom, max, $max-sm) {
		display: none;
	}
}

.error-wrap {
	max-width:1600px;
	margin: 0 auto;
	overflow: hidden;
	background-color: #fff;
	a {
		display:block;
		text-decoration:underline;
		color:#a40033;
	}
	.inner {
		position: relative;
		max-width:848px;
		margin:auto;
		padding: 294px 0;
		text-align: center;
		font-size: 18px; 
		line-height: 24px;
		color: #333;
	}
	.logo {
		margin-bottom: 26px;
	}
	.title {
		margin-bottom: 18px;
		font-size: 40px; 
		line-height: 48px;
		color: #000;
		@include font-family($font-semibold);
	}
	.msg1 {
		margin-bottom: 24px;
	}
	@media screen and (max-width:767px){
		br {display: none;}
		.inner {
			padding: 117px 24px 0 24px;
			font-size: 16px; 
			line-height: 24px;
			color: #6b6b6b;
		}
		.title {
			margin-bottom: 15px;
			font-size: 24px; 
			line-height: 28px;
		}
		.logo {
			margin-bottom: 15px;
		}
		.logo img {
			width: 113px;
			height: 54px;
		}
		.msg1 {
			margin-bottom: 23px;
		}
	} 	
}