@charset "UTF-8";

// 20200429 START 구유정 || rtl 추가 (AE 대응)
// 20200507 START 구유정 || rtl 수정 (AE 대응)
[dir=rtl] {
	body {
		text-align:right;
	}
	.modal {
		&.modal-simple {
			.modal-dialog {
				margin-left:0;
				margin-right:-200px;
				@include screen(custom, max, $max-sm){
					margin-left:0;
					margin-right:-155px;
				}
			}
		}
		.modal-dialog {
			left:auto;
			right:50%;
			text-align:right;
			&.modal-sm {
				margin-left:0;
				margin-right:-240px;
				@include screen(custom, max, $max-sm) {
					right:0;
					margin-left:12px;
					margin-right:12px;
				}
			}
			&.modal-md {
				margin-left:0;
				margin-right:-280px;
				@include screen(custom, max, $max-sm) {
					right:0;
					margin-left:12px;
					margin-right:12px;
				}
			}
			&.modal-lg {
				margin-left:0;
				margin-right:-355px;
				@include screen(custom, max, $max-sm) {
					right:0;
					margin-left:12px;
					margin-right:12px;
				}
			}
		}
	}
}
// 20200429 END 구유정 || rtl 추가 (AE 대응)
// 20200507 END 구유정 || rtl 수정 (AE 대응)

