// choice my products :: request repair step1 login user
.full-box {
	&.select-product{
		@include screen('md') {
			padding: 24px 30px;
		}
		@include screen('lg') {
			padding: 24px 30px;
		}
		.my-Products {
			position:relative;
			max-width:(1284px+30px);
			margin:0 auto;
			@include screen(custom, min, $min-md) {
                //padding:23px 30px 30px;
                padding:0px;
			}
			@include screen(custom, max, $max-sm) {
                //padding:14px 15px 20px;
                padding:0px;
				//margin-bottom: -20px;
			}
			.title {				
				// padding:25px 0 0 0;
				position:relative;
				@include screen(custom, max, $max-sm) {
					// padding:7px 0 0 0;
				}
				.link-text {
					position:absolute;
					right:-4px;
					top:10px;
					@include screen(custom, max, $max-sm) {
						position:static;
						margin-bottom:14px;
					}
				}				
				strong {
					@include font-family($font-semibold);
					font-size:24px;
					line-height:24px;
					color:$color-carmine;
					@include screen(custom, max, $max-sm) {
						font-size:16px;
						line-height:24px;
						margin-top: -1px;
					}
				}
				.desc {
					@include font-family($font-regular);
					font-size:16px;
					padding:2px 0 13px 0;
					color: $color-dimgray;
					@include screen(custom, max, $max-sm) {
						font-size:14px;
						line-height:20px;
						padding:0 0 3px 0;
					}
				}
			}
			.product-type {
				position:relative;
				width:100%;
				padding:20px 50px 23px;				
				max-height:305px;
				overflow:hidden;
				@include screen(custom, max, $max-sm) {
					padding: 24px 0;
					margin-bottom:0;
					max-height:203px;
				}
				.items {
					@extend %clearfix;
					width:100%;
					a {
						display:block;
					}
					.slick-list {
						width:auto;
						height:100%;
						overflow:hidden;
					}
					.slick-arrow {
						padding:0;
						&.slick-prev,
						&.slick-next {
							position:absolute;
							top:105px;
							width:27px;
							height:46px;
							transform: translate(0,-50%);
						}
						&.slick-prev {
							left:20px;
						}
						&.slick-next {
							right:20px;
						}
						&.slick-disabled {
							opacity:0.3;
						}
						@include screen(custom, max, (1284+30)) {
							&.slick-prev {
								left:20px;
							}
							&.slick-next {
								right:20px;
							}
						}
					}
					opacity:0;
					&.slick-initialized {
						opacity:1;
					}
				}
				.item {
					position:relative;
					float:left;
					padding:20px 0 0;
					outline: none;
					@include screen(custom, max, $max-sm) {
						padding: 0;
					}
					@include screen(custom, min, $min-lg) {
						&:nth-child(4n-3) {&:before {display:none;}}
					}
					@include screen(md) {
						&:nth-child(3n-2) {&:before {display:none;}}
					}
					@include screen(custom, max, $max-sm) {
						&:nth-child(2n-1) {&:before {display:none;}}
					}
					.visual,
					.model-name {
						width: 100%;
						text-align: center;
						margin:0 auto;
					}
					.visual {
						// background:rgba(165,0,52,0.2);
						// img {width:100%;height:auto;}
						width: auto;
						max-width:150px;
						a {
							display:block;
						}
						img {
							max-width: 100%;
						}
						@include screen(custom, max, $max-sm) {
							padding: 0 15px;
							box-sizing: border-box;
							max-width:100px;
						}
					}
					.model-name {
						// min-height:290px;
						// background:rgba(165,0,52,0.2);
						margin-top:20px;
						padding: 0 5px;
						text-align:center;
						@include screen(custom, max, $max-sm) {
							margin-top:15px;
						}
						.text {
							//height: 59px;							
							color: $color-black;
								font-size: 16px;
								line-height: 20px;
								display:block;
								@include screen(custom, max, $max-sm) {
									font-size: 14px;
									line-height:14px;
								}
							@include screen(custom, max, $max-sm) {
								height: auto;
								// margin-bottom: 13px;
							}							
						}
					}
				}
		
				// slick carousel stylesheet
				.slick-slider {
					button {
						appearance: none;
						border: none;
						background-color: transparent;
						text-indent: -9999em;
						// line-height: 0;
						// outline: none;
					}
					.slick-track {
						margin-left: auto;
						margin-right: auto;
					}
					.slick-arrow {
						line-height:0;
						svg {
							display: block;
						}
						color: $color-gray;
						&:hover,
						&:focus {
							color: $color-nightrider;
						}
					}
					.slick-dots {
						font-size: 0;
						text-align: center;	
						margin-top:19px;
						height:12px;
						@include screen(custom, max, $max-sm) {
							margin-top: 20px;
							margin-bottom:0;
							height:10px;
						}
						li {
							display: inline-block;
							width: 12px;
							height: 12px;
							position: relative;
							border-radius: 50%;
							margin-left: 10px;
							line-height:normal;
							&:first-child {
								margin-left: 0;
							}
							@include screen(custom, max, $max-sm) {
								width:10px;
								height:10px;
								margin-left: 8px;
							}
							a, button {
								background: $color-gray;
								display:block;
								width:100%;
								height:100%;
								margin:0;
								padding:0;
								border:0;
								border-radius:50%;
								&:focus {
									outline:1px dotted;outline:5px auto -webkit-focus-ring-color;
								}
							}
							&.slick-active {
								a, button {
									background: $color-carmine;
								}
							}
							&:only-child {
								display:none;
							}
						}
					}
				}
			}

			// no have registered product
			.no-items {
				.no-content {
					padding:80px 0 68px;
					@include screen(custom, max, $max-sm) {
						padding:26px 0;
					}
					.guide-title {
						@include font-family($font-regular);
						font-size: 16px;
						line-height: 24px;
						color:$color-dimgray;
						@include screen(custom, max, $max-sm) {
							font-size: 14px;
							line-height: 20px;
						}
					}
				}
			}
		}
	}

	[dir=rtl] &.select-product {
		.my-Products .title .link-text {
			right:auto;
			left:-4px;
		}
		.my-Products .product-type .slick-slider .slick-dots li:first-child {
			margin-left:10px;
		}
	}
}

